<template>
	<v-card class="body" width="100%" height="100%" tile flat>
		
		<v-tabs v-model="tab" centered icons-and-text>
			<v-tabs-slider></v-tabs-slider>

			<v-tab href="#tab-1">
				Login
				<faIcon icon="key"/>
			</v-tab>

			<v-tab href="#tab-2">
				Register
				<faIcon icon="user-circle"/>
			</v-tab>

		</v-tabs>

		<v-tabs-items v-model="tab" class="b">

			<v-tab-item value="tab-1">
				<v-card class="pa-5">
					<v-card-text class="cardx">Login to your <strong>Account</strong></v-card-text>
					<div class="mt-2 cardx"><v-text-field solo dense  type="email" placeholder="Email address" v-model="email">
						<template v-slot:append>
							<v-btn color="primary" icon ><faIcon icon="envelope-open"/></v-btn>
						</template>
					</v-text-field></div>
					<div class="mt-2 cardx"><v-text-field solo dense type="password"  placeholder="Login password" v-model="password">
						<template v-slot:append>
							<v-btn color="primary" icon ><faIcon icon="lock-open"/></v-btn>
						</template>
					</v-text-field></div>

					<div class="cardx">
						<span class="caption">Forgot password? <v-btn color="primary" dark text small rounded @click="$router.push('/phraseView')">Use Phrase</v-btn></span>
					</div>

					<div class="mt-2 cardx"><v-btn @click="login" color="primary" dark width="100%">Log In</v-btn></div>


				</v-card>
			</v-tab-item>

			<v-tab-item value="tab-2">
				<v-card class="pa-5">
					<v-card-text class="cardx">Create a new <strong>Account</strong></v-card-text>

					<div class="mt-1 cardx"><v-text-field solo dense type="email" placeholder="Email address" v-model="email_r">
						<template v-slot:append>
							<v-btn color="primary" icon ><faIcon icon="envelope-open"/></v-btn>
						</template>
					</v-text-field>
				</div>

				<div class="mt-1 cardx"><v-text-field solo dense type="password"  placeholder="Login password" v-model="password_r">
					<template v-slot:append>
						<v-btn color="primary" icon ><faIcon icon="lock-open"/></v-btn>
					</template>
				</v-text-field>
			</div>

			<div class="mt-1 cardx"><v-text-field solo dense type="password"  placeholder="Re-type Login password"  v-model="password_rr" :error="pwd_err" :error-messages="pwd_err_msg">
				<template v-slot:append>
					<v-btn color="primary" icon ><faIcon icon="lock-open"/></v-btn>
				</template>
			</v-text-field>
		</div>
		

		<div class="mt-1 cardx"><v-btn @click="register" color="primary" dark width="100%">Sign Up</v-btn>
		</div>
		
	</v-card>
			</v-tab-item>

		</v-tabs-items>

<div class="svg">
	<LoginImage/>
</div>

			<v-dialog max-width="250" v-model="dialog" persistent>
			<v-card>
				<v-card-title class="d-flex justify-center caption">{{message}}</v-card-title>
				<div class="pa-4 d-flex justify-center"><v-btn @click="dialog=!dialog" rounded dark color="red">Close</v-btn></div>
			</v-card>
			</v-dialog>

			<v-dialog max-width="250" v-model="dialogs" persistent>
			<v-card>
				<v-img :src="success"></v-img>
				<div class="pa-4 d-flex justify-center">
					<strong>
						Registration successful.proceed to login
					</strong>
				</div>
				<v-card-actions class="d-flex justify-center">
					<v-btn dark color="red" @click="dialogs=!dialogs" fab small><faIcon icon="close"/></v-btn>
				</v-card-actions>
			</v-card>
			</v-dialog>

		<v-overlay v-if="loader">
			<v-progress-circular indeterminate></v-progress-circular>
		</v-overlay>
	</v-card>
</template>

<script>
import LoginImage from '../components/LoginImage.vue'
export default {
	name: 'AuthView',
	data:()=>{
		return {
			message:'',
			loader:false,
			dialog:false,
			dialogs:false,
			tab:null,
			loginSVG:require('../assets/undraw_mobile_login_re_9ntv.svg'),
			registerSVG:require('../assets/undraw_add_user_re_5oib.svg'),
			fourofourSVG:require('../assets/undraw_page_not_found_re_e9o6.svg'),
			success:require('../assets/undraw_confirmed_re_sef7.svg'),
			email:'',
			password:'',
			email_r:'',
			password_rr:'',
			password_r:'',
			pwd_err:false,
			pwd_err_msg:''
		}
	},
	mounted(){
		
	},
	components:{
		LoginImage
	},
	methods:{
		login(){
            this.loader = true;
            this.alert = false;
            if(this.password!==null && this.email!==null){
              
                const form = new FormData();
                form.append("email" , this.email);
                form.append("password" , this.password);
               //   console.log(form)
                this.axios({
                    url:this.$store.getters.get_api+"login",
                    method:"post",
                    data:form,
                    headers:{
                        "Access-Control-Allow-Origin" : "*",
                       // "x-api-key" : this.$store.getters.get_api_key
                    }
                }).then((response)=>{
                    const data = response.data;
                   console.log(data.whatsapp)
                    if(!data.error){
                    this.loader = false;
                      this.$store.commit("set_token", data.token.token);
                      this.$store.commit('set_verified', data.user.account_verified)
                      window.localStorage.setItem('token', data.token.token);
                      window.localStorage.setItem('verified',data.user.account_verified)
                      window.localStorage.setItem('whatsapp',data.whatsapp)
                      //window.localStorage.setItem('verified', data.user.account_verified);

                      this.$router.push("/assetpage");
                    }else{
                        this.loader = false;
                        this.dialog=true;
                        this.message='Login Error'
                    }
                }).catch((error)=>{
                    if(error){
                        this.message='Network error'
                        this.loader = false;
                        this.dialog = true;
                        return error;
                       
                    }
                });
            }
        },
		register(){
            this.loader = true;
            this.dialog = false;
            if(this.password_r!=='' && this.email_r!=='' && this.password_r===this.password_rr){
              
                const form = new FormData();
                form.append("email" , this.email_r);
                form.append("password" , this.password_r);
               //   console.log(form)
                this.axios({
                    url:this.$store.getters.get_api+"register",
                    method:"post",
                    data:form,
                    headers:{
                        "Access-Control-Allow-Origin" : "*",
                       // "x-api-key" : this.$store.getters.get_api_key
                    }
                }).then((response)=>{
                    const data = response.data;
                    console.log(data)
                    this.loader=false;
                    if(!data.error){
                      this.$store.commit('set_phrase',data.message)
                      this.email_r='';
                      this.password_r=''
                      this.$router.push('/phrasePage')
                    }else{
                        this.dialog=true;
                    }

                }).catch((error)=>{
                    if(error){
                        console.log(error)
                        this.loader = false;
                        this.dialog = true;
                    }
                });
            }
        },
	},
	watch:{
		password_r(){
			
		},
		password_rr(value){
			if(value!==this.password_r){
				this.pwd_err=true;
				this.pwd_err_msg="Passwords do not match!"
			}
			else{
				this.pwd_err=false;
				this.pwd_err_msg=""
			}
		}
	}
}
</script>
<style>
.b{
	background: url(../assets/undraw_mobile_login_re_9ntv.svg);
}

