import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    dark:false,
    //api_link:`http://${window.location.hostname}:3333/api/`,
    api_link:`https://api.skylineinu.finance/api/`,
    token:undefined,
    phrase:'',
    notice:[],
    selected_asset:{},
    selected_asset_icon:'',
    account_verified:0,
    ////
    socket: {
      isConnected: false,
      message: '',
      reconnectError: false,
    },
    exchange_link:'https://api.blockchain.com/v3/exchange/'
    ////
  },
  getters: {
    get_phrase:(state)=>state.phrase,
    get_api:(state)=>state.api_link,
    get_notice:(state)=>state.notice,
    get_token:(state)=>state.token,
    get_selected_asset:(state)=>state.selected_asset,
    get_selected_asset_icon:(state)=>state.selected_asset_icon,
    get_exchange_link:(state)=>state.exchange_link,
    get_verified:(state)=>state.account_verified
  },
  mutations: {
    set_token(state,value){
      state.token = value;
    },
    set_mode(state,value){
      state.mode = value;
    },
    set_selected_asset(state,value){
      state.selected_asset = value;
    },
    set_selected_asset_icon(state,value){
      state.selected_asset_icon = value;
    },
    set_trans(state , value){
      state.trans = value
    },
    set_verified(state , value){
      state.account_verified = value;
    },
    set_phrase(state,value){
      state.phrase=value
    },
    set_notice(state,value){
      state.notice=value
    }
    
  },
  actions: {
    totalNotice:async function(context){
      axios({
        url:context.getters.get_api+"fetchNotification",
        method:"get",
        headers:{
          'Access-Control-Allow-Origin':'*',
          'Authorization':`Bearer ${context.getters.get_token}`
        }
      }).then((res)=>{
        const data = res.data;
        if(!data.error){
         // console.log(data.message)
          context.commit('set_notice',data.message)
        }
      }).catch((error)=>{
        //console.log(context)
        return error
      })
     },
    authenticate:async function(context){
     console.log(window.localStorage.getItem('token'))
       if(window.localStorage.getItem('token')!==undefined){
        // console.log('verified '+window.localStorage.getItem('verified')
         await context.commit('set_token',window.localStorage.getItem('token'));
          await context.commit('set_verified',parseInt(window.localStorage.getItem('verified')))
       }
     },
     selected_asset:async function(context){
       if(window.localStorage.getItem('selected_asset')!==undefined){
          await context.commit('set_selected_asset',window.localStorage.getItem('selected_asset'))
        // await context.commit('selected_asset_icon',window.localStorage.getItem('selected_asset_icon'))
       }
     }
  },
  modules: {
  }
})
