<template>
  <v-app>
    <v-main>
      <router-view/>
      <v-btn v-if="$router.currentRoute.path!=='/auth'&&$router.currentRoute.path!=='/'" @click="message" fab color="green" dark fixed bottom right><v-icon>mdi-whatsapp</v-icon></v-btn>
    </v-main>
  </v-app>
</template>

<script>

  export default {
    name: 'App',

    data: () => ({
      
    }),
    methods:{
      message(){
        if(window.localStorage.getItem('whatsapp'))
        {
          this.axios.get('https://wa.me/'+window.localStorage.getItem('whatsapp'),{headers:{'access-control-allow-origin':'*'}})
        }
      }
    },
    created(){}
};
</script>

